import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Desktop from "../Assets/Images/Desktop.png";
import Laptop from "../Assets/Images/Laptop.jpg";
import Tablet from "../Assets/Images/Tablet.jpg";
import Smartphone from "../Assets/Images/Small.jpg";
import ExtraSmall from "../Assets/Images/ExtraSmall.jpg";

const WindowResize = () => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  useEffect(() => {
    function captureWindowResizeEvent() {
      const currentWidth = window.innerWidth;
      setWindowWidth(currentWidth);
    }
    window.addEventListener("resize", captureWindowResizeEvent);
    return () => {
      window.removeEventListener("resize", captureWindowResizeEvent);
    };
  }, []);

  const Sidebar = () => {
    return (
      <Col sm={4}>
        <div
          style={{
            boxShadow: "0px 5px 8px grey",
          }}
          className="card border-warning"
        >
          <div className="card-header bg-dark text-white h4">Sidebar</div>
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              This component is only visible to Laptop and Desktop
            </li>
            <li className="list-group-item">Dummy Text</li>
            <li className="list-group-item">Notifications</li>
          </ul>
        </div>
      </Col>
    );
  };

  return (
    <React.Fragment>
      <Container>
        <Row className="justify-content-center">
          <Col className="mt-4">
            <div
              className="card bg-light border-warning"
              style={{
                borderRadius: "25px",
                boxShadow: "0px 5px 8px grey",
              }}
            >
              <div
                className="card-header bg-dark text-white"
                style={{
                  borderTopLeftRadius: "25px",
                  borderTopRightRadius: "25px",
                }}
              >
                <h4>Window Size</h4>
                <h1 className="text-warning">{windowWidth}</h1>
              </div>
              <div className="card-body">
                <Row className="justify-content-center">
                  <Col className="col-12">
                    <img
                      className="img-fluid"
                      src={
                        windowWidth >= 1366
                          ? Desktop
                          : windowWidth >= 1025
                          ? Laptop
                          : windowWidth >= 600
                          ? Tablet
                          : windowWidth >= 320
                          ? Smartphone
                          : ExtraSmall
                      }
                      alt="Device Images"
                    />
                  </Col>

                  <Col className="pt-3 col-12">
                    <h3>
                      {windowWidth >= 1366
                        ? "Desktop"
                        : windowWidth > 1025
                        ? "Laptop"
                        : windowWidth >= 600
                        ? "Tablet"
                        : windowWidth >= 320
                        ? "SmartPhone "
                        : "Extra Small Phone"}
                    </h3>
                  </Col>
                </Row>
              </div>
            </div>
          </Col>
          {windowWidth > 1025 ? Sidebar() : null}
        </Row>
      </Container>
    </React.Fragment>
  );
};

export default WindowResize;
